import '../more.css'

import React, { useState } from 'react';
import { pAcData } from './pAc_questions';
import styled from 'styled-components';
import { FiPlus, FiMinus } from 'react-icons/fi';
import pacpimg1 from '../../Assets/Product and Company Consulting_Imag1.png';
import pacpimg2 from '../../Assets/Product and Company Consulting2.png';
    const Wrap = styled.div`
    background: #FFFFFF;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    text-align: center;
    cursor: pointer;
    h1 {
      padding: 2rem;
      font-size: 1rem;
    }
   
  `;
  
  
  
  const PAcFaq = () => {
    const [clicked, setClicked] = useState(false);
  
    const toggle = index => {
      if (clicked === index) {
        //if clicked question is already active, then close it
        return setClicked(null);
      }
  
      setClicked(index);
    };  
    return ( <>
        <div className="project_img">
        <div className="project_img_div">
        <img src={pacpimg1} alt="new" />
        </div>
        <div className="project_img_div">
        <img src={pacpimg2} alt="new" />
        </div>
    </div>
        <div className='more_faq_main'>
          <div><h1 className='more_faq_main-h1'><b>Here Are The Ways We Can Help</b></h1></div>  
          <div className='more_faq_main_secdiv'>
              {pAcData.map((item, index) => {
                return (
                  <>
                    
                    <Wrap onClick={() => toggle(index)} key={index}>
                      <h1 className='more_h1size font-med'>{item.question}  
                      <span>{clicked === index ? <FiMinus /> : <FiPlus />}</span>
                      </h1>
                      
                      
                    </Wrap>
                    
                    {clicked === index ? (
                    
                        <p className='more_psize font-med'>{item.answer}</p>
                      
                    
                    ) : null}
                    
                  </>
                );
              })}
           
           </div> 
        </div> </>
      );
    };

export default PAcFaq;