import React from "react";
import './recentproject.css';
import recent from '../../Assets/ResentProjectsimage.png';
import rectangle from '../../Assets/Rectangle 4.png';
function RecentProject() {
 
    return ( 
        <>
        <div className='orbit__Recent'>
            <br/><br/><br/><br/>
        <span>Recent Projects</span>
        <p>Every Month, Millions Of People Use <br/> The Products We've Built</p>
        <div className='orbit__Recent-image'>
            <img src={recent} alt=''></img>
        </div>  
    </div>
    <div className='orbit__Client'>
        <div className='orbit__Client-header'>
            <span>Client Stories</span>
            <p>Designing Better Experiences Is A Journey. Here <br/> Are The Stories Behind The Products.</p>
        </div>
        <div className='orbit__Client-story'>
            <div className='orbit__Client-story-img'>
                <img src={rectangle} alt=''></img>
            </div>
            <div className='orbit__Client-story-data'>
                <div className='orbit__Client-story-name'>
                    <h1 className='font-med'>Ron Gasper</h1>
                    <span>Mast Hair Inc</span>
                </div>

                <div className='orbit__Client-story-quote '>
                    <p className='font-med'>"Orbit Prism has  completely reinvented  the way that we work  across our  organization".</p>
                </div>

                <div className='orbit__Client-story-link'>
                <button> <a href="./clientstories">Read Ron's story</a></button>
                   
                </div>


            </div>
        </div>
    </div>
        
        </>
     );
}

export default RecentProject;