import React from "react";
import './designAnewProduct.css';
import maskgroup from '../../Assets/Mask Group 5.png';
import newsimg from '../../Assets/Unstrucketed.png';
import eduimg from '../../Assets/Child Education App_image 1.png';
function Projects() {
    return (

        <>
        <div className="project__">   <h1 className="font-med">Projects</h1></div>
        <br />
         <div className="project_main_image">
             <img src={maskgroup} alt="new" /></div>
         <br></br>
         <div className="projects_main">
             <p className="font-med">Getty Images Was Seeing A Lot Of Traffic From Consumers Outside Of Their Target Audience And Saw An Opportunity To Turn<br/> That Traffic Into Revenue. The Plan Was To Create A New Sub-Brand Of Getty Focused On A Broader Audience Beyond Their B2B<br/> Clients. We Set An Editorial Vision For The Platform, Designed And Named The Brand, As Well As Built A Responsive Web <br/>Experience From Scratch. User Testing Ensured The Work Resonated With This New Audience
        </p>
          </div>
          <br/>
          <br/>
         
          <div className="project_img">
              <div className="project_img_div">
              <img src={newsimg} alt="new" />
              </div>
              <div className="project_img_div">
              <img src={eduimg} alt="new" />
              </div>
          </div>
        
        
        </>
      );
}

export default Projects;