import React from "react";
import './ourway.css'
import imgO from '../../Assets/Rectangle 3.png';
function Ourway() {
    return ( 
        <>
        <div className="ourway__main">
            <div className="ourway_number">
            <h5 className="font-reg">1</h5></div>
           <div className="ourway__other_div">
                <span className="font-med">Belief</span>
                <h3 className="font-reg">Our long-avowed goal is to be the product team of choice for high-growth businesses.</h3>
                 <p className="font-reg">To be successful we must keep our promises, build quality products at scale, grant unusual autonomy to our team members, and partner with our clients through thick and thin (though we prefer thick and thicker).</p>
            </div>
        </div>
        <div className="ourway__otherdiv">
            <div className="ourway_number">
            <h5 className="font-reg">2</h5></div>
           <div className="ourway__other_div">
                <span className="font-med">Mission</span>
                <h3 className="font-reg">We inspire and empower organizations to build and operate great products.</h3>
                 <p className="font-reg">We do this by leading the strategy, design and development process for our clients. It's about more than just expertise; we bring passion, curiosity and experience to every project.</p>
            </div>
        </div>
        <div className="ourway__otherdiv">
            <div className="ourway_number">
            <h5 className="font-reg">3</h5></div>
           <div className="ourway__other_div">
                <span className="font-med">Horizon</span>
                <h3 className="font-reg">We plan for longevity.</h3>
                 <p className="font-reg">Things change quickly in this industry and we take a long-term view in all decision making. Today's best practices might not be tomorrow's so we test new paths-and ultimately produce better results.</p>
            </div>
        </div>
        <div className="ourway__otherdiv">
            <div className="ourway_number">
            <h5 className="font-reg">4</h5></div>
           <div className="ourway__other_div">
                <span className="font-med">Projects</span>
                <h3 className="font-reg">We specialize in building category-defining products and companies, going from 0 + 1.</h3>
                 <p className="font-reg">We specialize in building category-defining products and companies, going from 0 + 1.</p>
            </div>
        </div>
        <div className="ourway__otherdiv">
            <div className="ourway_number">
            <h5 className="font-reg">5</h5></div>
           <div className="ourway__other_div">
                <span className="font-med">Lifespan</span>
                <h3 className="font-reg">We work on a "Build, Operate, Transfer" model.</h3>
                 <p className="font-reg">Each project starts with a shared understanding of the endpoint. We help our clients to plan for the future and empower them to operate their product independently. We train, teach and help put the right team in place.</p>
            </div>
        </div>
        <div className="ourway__otherdiv">
            <div className="ourway_number">
            <h5 className="font-reg">6</h5></div>
           <div className="ourway__other_div">
                <span className="font-med">Approach</span>
                <h3 className="font-reg">We are big believers in the power of keeping it simple.</h3>
                 <p className="font-reg">And we only take on projects we know we can execute. We've built our team with deep expertise in a few specialized areas and we employ top talent.</p>
            </div>
        </div>
        <div className="ourway__otherdiv">
            <div className="ourway_number">
            <h5 className="font-reg">7</h5></div>
           <div className="ourway__other_div">
                <span className="font-med">Constraints</span>
                <h3 className="font-reg">Most technology projects fail because people misjudge the variability of their constraints.</h3>
                 <p className="font-reg">Constraints are living, breathing things. Understanding how they change is equal parts art and science. It is our job to help you prioritize and understand the forest, instead of just the trees.</p>
            </div>
        </div>
        <div className="ourway__otherdiv">
            <div className="ourway_number">
            <h5 className="font-reg">8</h5></div>
           <div className="ourway__other_div">
                <span className="font-med">Clarity</span>
                <h3 className="font-reg">We strive for complete clarity in what we do.</h3>
                 <p className="font-reg">Most consultants and agencies make it hard to really know what its like to work with them. Projects are obscured. Processes are hidden. We work hard on being clear, focused and human.</p>
            </div>
        </div>
        <div className="ourway_image">
        <img src={imgO} alt=""></img>
            </div>
        </>
    )}

export default Ourway;