import './ventureCapitalFunding.css'
import React, { useState } from 'react';
import { VentureData } from './venture_questions';
import styled from 'styled-components';
import { FiPlus, FiMinus } from 'react-icons/fi';

const Wrap = styled.div`
  background: #FFFFFF;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  text-align: center;
  cursor: pointer;
  h1 {
    padding: 2rem;
    font-size: 1.5rem;
  }
 
`;



const VentureFaqs = () => {
  const [clicked, setClicked] = useState(false);

  const toggle = index => {
    if (clicked === index) {
      //if clicked question is already active, then close it
      return setClicked(null);
    }

    setClicked(index);
  };

  return (
    <div className='faq_main_white'>
      <div><h1 className='faq_main-h1_white font-sbold'>FAQS</h1></div>  
      <div className='faq_main_secdiv_white'>
          {VentureData.map((item, index) => {
            return (
              <>
                
                <Wrap onClick={() => toggle(index)} key={index}>
                  <h1 className='h1size_white font-med'>{item.question}  
                  <span>{clicked === index ? <FiMinus /> : <FiPlus />}</span>
                  </h1>
                  
                  
                </Wrap>
                
                {clicked === index ? (
                
                    <p className='psize_white font-med'>{item.answer}<br/><br/><br/>{item.answer1}{item.answer2}<br/><br/><br/>{item.answer3}<br/><br/><br/></p>
                  
                
                ) : null}
                
              </>
            );
          })}
       
       </div>
  
     
       
    
    </div>
  );
};

export default VentureFaqs;