import React from "react";
import '../more.css';
import newproduct2 from '../../Assets/Build an Existing product.svg';
import './improveAnExistingProduct.css';
import Footer from '../footer/footer';
import ImproveWaysOfHelp from '../improveAnExistingProduct/improveWaysOfHelp';
import WeCanHelpWhite from '../wecanhelpwhite';
import ExistingProjects from '../improveAnExistingProduct/existingProject';
import ImproveFaqs from "./improveFaqs";
function ImproveAnExistingProduct() {
    return ( 
        <>
        <div className="morepages_main">
        <div>
        <h1 className="font-med"> Improve an existing product.</h1><br/>
        
        
    <p className="font-reg">Focus, prioritize, and turn your <br/>product into a category leader.</p>
        </div></div>
        <div className="icondiv">
        <img src={newproduct2} alt="new" />
        <h1 className="font-med">Improve an existing product.</h1><br/>
        <p  className="font-reg">When improving a product that’s already on the market, you have to balance the needs of your <br/> team and existing customers with your future goals. We’re here to help you navigate the process.<br/> We bring an outside perspective to find new ways to break with convention and redefine a <br/> product category. With a robust customer interview process, we identify key opportunities to <br/> improve and grow the product to better serve customers—new and old.</p>
        </div>
        <ImproveFaqs/>
        <ImproveWaysOfHelp/>
        <ExistingProjects/>
        <WeCanHelpWhite/>
        <Footer/>
        </>
     );
}

export default ImproveAnExistingProduct;