import React from "react";
import './privatetool.css'
function PrivateTool() {
    return ( 

<>
<div className="privatetool__main">
    <h1 className="font-reg">Our clients get access to our <br/> private tools and insights</h1>

<div className="tools__main">
    <div>
        <div>
            <span className="font-sbold">01</span>
            <h4 className="font-sbold">Alpha Score</h4>
            <p className="font-reg">The <b>Alpha Score</b> is single simple metric to measure the digital maturity of your <br/>company. This evaluation framework helps you better understand your firm's<br/> digital readiness across five dimensions, measuring value that a differentiating<br/> process creates compared to a benchmark competitive process.

            </p>
            <h4 className="font-sbold">Request an Alpha Score assessment</h4>
        </div>

    </div>
    <div>
    <div className="insidediv">
            <span className="font-sbold">02</span>
            <h4 className="font-sbold">Raw Materials</h4>
            <p className="font-reg">This is our internal Versett knowledge repository of the best of what we have<br/> read across the internet, academia, books and everywhere else. Read through <br/> hundreds of articles, resources and writing from all corners of the world. As one<br/> of our clients put it, "I used to read Twitter, now I just check this."
            </p>
            <h4 className="font-sbold">Request Access</h4>
        </div>

    </div>
</div>

<div className="tools__main">
    <div>
        <div>
            <span className="font-sbold">03</span>
            <h4 className="font-sbold">Private Newsletter</h4>
            <p className="font-reg">Our private client newsletter covers the topics we are thinking about in a more<br/> raw format. This is our way of sharing the things we are exploring currently and <br/>how they may apply to our broader practice. We collect the things that we see, <br/>pull them apart into smaller bits that we can put back together in new and <br/> interesting ways to share with you.
            </p>
            <h4 className="font-sbold">Request Access</h4>
        </div>

    </div>
    <div>
    <div className="insidediv">
            <span className="font-sbold">04</span>
            <h4 className="font-sbold">Events</h4>
            <p className="font-reg">Join us for discussions on the intersection of technology, culture and business.<br/> Anchored by our Start Here series, exploratory conversations on the topics we <br/>can't stop thinking about. In each discussion, we walk through the history and<br/> development of the concept through to where we are today. We canvas what<br/> the researchers say, opine on the popular literature on the topic, and observe <br/> practical examples in the market.
            </p>
            <h4 className="font-sbold">Sign up for the next event</h4>
        </div>

    </div>
</div>



</div>


</>

     );
}

export default PrivateTool;