import React from "react";
import '../more.css';
import npimg1 from '../../Assets/Design a new Product_Image.png';
import npimg2 from '../../Assets/Design a new Product2.png';


function WaysOfHelp() {
    return ( <>
    
    <div className="project_img">
              <div className="project_img_div">
              <img src={npimg1} alt="new" />
              </div>
              <div className="project_img_div">
              <img src={npimg2} alt="new" />
              </div>
          </div>
          <div className="wecanhelp_white_main">
            <div> <h4 className="wecanhelpwhite_main-h4 font-med">Over the course of the project,<br/> typically 3-6 months, <br/>our combined  teams will:</h4>
         <br></br> <h4 className="wecanhelpwhite_main-h3 font-med">Interested in working with Us?</h4></div>
           
            <div className="help_links_white">
              <div> <span className="font-med">01 </span> <button className="font-med"> Create a prioritized product roadmap</button> </div>
           
               <br/><br/>
             <div><span className="font-med">02 </span><button className="font-med" > Conduct customer interviews and market</button></div>  
                 <br/><br/>
              <div><span className="font-med">03 </span> <button className="font-med">Perform design work cycles</button></div> 
      
                 <br/><br/>
                 <div><span className="font-med">04 </span><button className="font-med"> Perform usability testing</button> </div>
                  <br/><br/>
                  <div><span className="font-med">05 </span><button className="font-med">Train your team to run the product</button></div>
                   <br/><br/>
            </div>
        </div>
    </> );
}

export default WaysOfHelp;