import React from "react";
import './careers.css';
import rec1 from '../../Assets/Rectangle 101.png';
import FooterBlack from '../footer/footerblack';
import { Link } from "react-router-dom";
function Careers() {
    return (  
        <>
       <div className="career_main">
        <div>
        <h1 className="font-med"> Careers</h1><br/>
        
        
    <p className="font-reg">A culture of innovation</p>
        </div>
        </div>
        <div className="career_main_div">
     <div className="career_pic"><img src={rec1} alt="new" /></div>
        
<div className="career_para"><p className="font-light">Orbit Prism is designed to be a place for doing meaningful work. Our team is tight- <br/>knit and dedicated to building great products. Send us something you've made <br/> that you're proud of and we'll be in touch. View all of our open roles</p>

      <span> <Link to='/openRoles'>View all of our open roles</Link></span>
       </div>  </div>
        <FooterBlack/>
        </>
    );
}

export default Careers;