import React from "react";
import img1 from '../../Assets/Rectangle 12.png'
import img2 from '../../Assets/Rectangle 14.png'
function BadHairDays() {
    return ( <> 
    <div className="badhairs">
        <div className="badhairs-title">
     <h1>   No Bad Hair Days.</h1>
<p>
How Rob Gaspar, a veteran hair stylist,<br />
 is taking his traditional brick and mortar <br />
  business to the web.

</p>
        </div>
        <div className="badhairs-img">
        <img src={img1} alt="" />
        </div>
        <div className="badhairs-content">
        <p>
        Grocery shopping, banking, buying shoes, dating - people will do almost anything online these days. But there isn't an app that can cut your hair (yet), so the salon industry is still largely an offline world. <br />
</p> <p>
When it comes to personal grooming - and stocking up on your favourite hair products - most of us rely on brick and mortar shops. And maybe because of this, consumers don't enjoy the same selection and availability in hair care products that they might for other goods. Rob Gaspar set out to solve this problem. He's bringing the individualized service and special attention you get in a salon chair to his new online shop. <br />
</p>
<p>
Since the age of 15, Rob has worked in salons. "I started out shampooing hair and mopping floors, but I was determined about where wanted to be. I didn't just want to be a great hairdresser, I wanted to own my own shop," he explains.
</p> <p>
Rob has been mastering every aspect of the hair industry for the past 25 years. After opening his own salon, Mast Hair in 2018, he set his sights on a new opportunity. "I would work on clients for ten hours a day, but I knew there was more to it than just being behind the chair. I started seeing weaknesses in men's hair care lines, and that got me thinking," he says
        </p>
       <div className="quote"> <span>
        "I would work on clients for ten hours a day, but I knew there was more to it than just being behind the chair. I started seeing weaknesses in men's hair care lines, and that got me thinking."
        </span>
        </div>
        <p>The product that's out there is either really expensive or really cheap. You have barber shop brands or cheap brands that cater to drug stores. I knew from working with my clients that men just wanted something that was clean and high-quality, with branding that connected to them," he says. "I knew that if I could develop a product I liked, there would be a market for it."
</p> <p>
The process for developing and marketing haircare products hasn't changed much over the years. Traditionally, you create something, put it in a bottle and push it to distributors. Rob set out to change that. He created his own line and brought it directly to his customers through his salon and website. "I wanted something that got rid of the fluff. Something catering to guys who are busy, travelling and on the go-but who are spending $50 on a haircut and want something nice for themselves," he explains. "I took all the best aspects of men's lines I've worked with, and created my own."</p>
       
        </div>
        <div className="badhairs-img margin">
        <img src={img2} alt="" />
        </div>

    </div>
    
    
    
    
    </> );
}

export default BadHairDays;