import React from "react";
import '../more.css';
import './launchAnewCompany.css';
import newproduct3 from '../../Assets/Page-1.svg';
import Footer from '../footer/footer';
import WeCanHelpWhite from '../wecanhelpwhite';
import LaunchAnewCompanyProjects from '../launchAnewCompany/launchAnewCompanyProjects';
import LaunchWaysOfHelp from '../launchAnewCompany/launchWaysOfHelp';
import LaunchFaq from "./launch_faqs";
function LaunchAnewCompany() {
    return ( 
        <>
        <div className="morepages_main">
        <div>
        <h1 className="font-med"> Launch a new Company</h1><br/>
        
        
    <p className="font-reg">Take your vision from  <br></br>concept to launch.</p>
        </div></div>
        <div className="icondiv">
    <img src={newproduct3} alt="new" />
    <h1 className="font-med">Launch a new Company</h1><br/>
    <p  className="font-reg">You might have an idea, some sketches or a prototype. We’ll help you quickly build your MVP for <br/>launch or an investor pitch. After years of working with high-growth companies, we have the <br/>speed and agility it takes to build a business from scratch. Our research, design and development <br/> team help take your company from idea to launch.</p>
    </div>
    <LaunchFaq/>
    <LaunchWaysOfHelp/>
    <LaunchAnewCompanyProjects />
    <WeCanHelpWhite/>
    <Footer/>
  
        </>
     );
}

export default LaunchAnewCompany;